.o-button {
  align-items: center;
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  margin-top: 20px;
  padding: 8px 17px;
  vertical-align: baseline;
  border: none;
  border-radius: $global-border-radius;
  background: get-color(blue);
  box-shadow: none;
  -webkit-appearance: none;
  line-height: 1;
  color: get-color(unique, white);
  @include font-size(1.7);
  font-weight: bold;
  line-height: 1;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  
  &:hover {
    opacity: 0.9;
    text-decoration: none;
  }
  
  &:first-child {
    margin-top: 0;
  }

  i {
    padding-left: 10px;
    @include font-size(2.1);
    line-height: .7;
    
    &:first-child {
      padding-left: 0;
      padding-right: 10px;
    }
  }

  &--light {
    background-color: get-color(unique, white);
    color: get-color(blue);
  }
}

.o-micro {
  display: inline-block;
  padding: 25px;
  position: relative;
  border: 1px solid $global-border-color;
  border-radius: $global-rounded-border-radius;
  @include font-size(4.4);
  line-height: 0.7;
  
  &:after {
    content: '';
    height: 7px;
    position: absolute;
    right: 25px;
    top: 20px;
    width: 7px;
    border-radius: $global-rounded-border-radius;
    background: red;
  }
  &__pulse,
  &__pulse:before {
    display: block;
    height: 130%;
    padding: 45px;
    position: absolute;
    left: 50%;
    opacity: 0;
    top: 50%;
    width: 130%;
    transform: translate(-50%, -50%);
    border: 1px solid get-color(blue);
    border-radius: $global-rounded-border-radius;
  }
  // &__pulse:before {
  //   content: '';
  //   height: 130%;
  //   width: 130%;
  // }
}