// 1.Config


// 2.Base
.l-banner {
  align-items: center;
  display: flex;
  justify-content: center;
  overflow: hidden;
  padding: 120px 0;
  position: relative;
  background-color: get-color(blue);
  color: get-color(unique, white);
  text-align: center;
  
  &__cover {
    height: auto;
    left: 0;
    height: 100%;
    opacity: 0.2;
    position: absolute;
    top: 0;
    width: 100%;
    background: url(../images/smartsteuer-hero.jpeg) no-repeat center / cover;
  }
  &__body {
    max-width: 680px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
    position: relative;
    z-index: 100;
  }
}
.l-main {
  padding-top: 80px;
}
.l-contentinfo {
  padding-bottom: 50px;
  padding-top: 50px;
  @include font-size(1.5);
  
  &__constrained {
    @extend %constrained-defaults;
    align-items: center;
    display: flex;
    justify-content: space-between;
  }
  a {
    color: get-font(color);
  }
}

.l-section {
  padding-top: 100px;
}


.l-section--features {
  margin-top: 120px;
  padding-bottom: 120px;
  padding-top: 120px;
  background: get-color(gray, light);
  text-align: center;
}