.o-slider {
  @include list-reset();
  white-space: nowrap;
  overflow: scroll;

  &__item {
    display: inline-block;
    margin-top: 0;
    padding-left: 30px;
    vertical-align: middle;
    
    img {
      max-height: 400px;
      max-width: 700px;
      width: 100%;
    }
  }
  &__caption {
    @include font-size(2.3);
  }
}