// 1.Config
// Add or remove breakpoints
$column-class-prefix: 'l-u--' !default;
$max-wrapper-width: 1140px !default;
$max-columns: 12 !default;
$gutter-width: 60px !default;
$gutter-height: 60px !default;
$prevent-spacing: 40px !default;

%constrained-defaults {
  margin-left: auto;
  margin-right: auto;
  padding-left: $prevent-spacing;
  padding-right: $prevent-spacing;
}

// 2.Base
// Name: Constrained-Container
.l-constrained {
  @extend %constrained-defaults;
  max-width: $max-wrapper-width;
  width: 100%;
}


// Name: Units-Wrapper
// Description: Each columns-area get a wrapper
.l-units {
  @include clearfix();
  @include list-reset();
  margin-left: -$gutter-width;

  &:first-child {
    margin-top: -$gutter-height;
  }
}

// Name: Units
// Description: Basic styling for each column
[class^="#{$column-class-prefix}"],
[class*="#{$column-class-prefix}"] {
  box-sizing: border-box;
  float: left;
  margin-top: $gutter-height;
  padding-left: $gutter-width;
}

// Name: Mobile-Grid
@for $sum from 1 through $max-columns {
  @for $i from 1 through $sum {
    .#{$column-class-prefix}#{$i}-#{$sum} {
      @include column($i, $sum);
    }
  }
}

// Name: Create columns for breakpoints
// Description: Loop through your max-columns and create your basic grid
@each $name, $size in $breakpoints {
  @for $sum from 1 through $max-columns {
    @for $i from 1 through $sum {
      .#{$column-class-prefix}#{unquote($name)}-#{$i}-#{$sum} {
        @media screen and (min-width: #{$size}) {
          @include column($i, $sum);
        }
      }
    }
  }
}