// 1.Config

// 2.Base
.is-hidden {
  display: none;
}

.is-hidden-mobile {
  display: none;
  
  @include respond(large) {
    display: inherit;
  }
}

.is-visible {
  display: inherit;
}
.is-visible-mobile {
  display: inherit;
  
  @include respond(large) {
    display: none;
  }
}

.is-aria-visible {
  height: 1px;
  overflow: hidden;
  position: absolute;
  width: 1px;
  clip: rect(0, 0, 0, 0);
}