.l-banner {
  align-items: center;
  display: flex;
  justify-content: center;
  overflow: hidden;
  padding: 120px 0;
  position: relative;
  background-color: #047fcd;
  color: #fff;
  text-align: center; }
  .l-banner__cover {
    height: auto;
    left: 0;
    height: 100%;
    opacity: 0.2;
    position: absolute;
    top: 0;
    width: 100%;
    background: url(../images/smartsteuer-hero.jpeg) no-repeat center/cover; }
  .l-banner__body {
    max-width: 680px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
    position: relative;
    z-index: 100; }

.l-main {
  padding-top: 80px; }

.l-contentinfo {
  padding-bottom: 50px;
  padding-top: 50px;
  font-size: 15px;
  font-size: 1.5rem; }
  .l-contentinfo__constrained {
    align-items: center;
    display: flex;
    justify-content: space-between; }
  .l-contentinfo a {
    color: #A1AAB3; }

.l-section {
  padding-top: 100px; }

.l-section--features {
  margin-top: 120px;
  padding-bottom: 120px;
  padding-top: 120px;
  background: #f5f4f5;
  text-align: center; }

.o-button {
  align-items: center;
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  margin-top: 20px;
  padding: 8px 17px;
  vertical-align: baseline;
  border: none;
  border-radius: 3px;
  background: #047fcd;
  box-shadow: none;
  -webkit-appearance: none;
  line-height: 1;
  color: #fff;
  font-size: 17px;
  font-size: 1.7rem;
  font-weight: bold;
  line-height: 1;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase; }
  .o-button:hover {
    opacity: 0.9;
    text-decoration: none; }
  .o-button:first-child {
    margin-top: 0; }
  .o-button i {
    padding-left: 10px;
    font-size: 21px;
    font-size: 2.1rem;
    line-height: .7; }
    .o-button i:first-child {
      padding-left: 0;
      padding-right: 10px; }
  .o-button--light {
    background-color: #fff;
    color: #047fcd; }

.o-micro {
  display: inline-block;
  padding: 25px;
  position: relative;
  border: 1px solid #ccc;
  border-radius: 100px;
  font-size: 44px;
  font-size: 4.4rem;
  line-height: 0.7; }
  .o-micro:after {
    content: '';
    height: 7px;
    position: absolute;
    right: 25px;
    top: 20px;
    width: 7px;
    border-radius: 100px;
    background: red; }
  .o-micro__pulse, .o-micro__pulse:before {
    display: block;
    height: 130%;
    padding: 45px;
    position: absolute;
    left: 50%;
    opacity: 0;
    top: 50%;
    width: 130%;
    transform: translate(-50%, -50%);
    border: 1px solid #047fcd;
    border-radius: 100px; }

.o-logo {
  display: inline-block;
  height: 75px;
  width: 75px;
  border-radius: 100px;
  background: url(../images/hauke.jpg) no-repeat center/100%; }

.o-avatar {
  display: block;
  height: 55px;
  position: relative;
  width: 55px;
  border-radius: 100px; }
  .o-avatar img {
    height: 55px;
    width: 55px;
    border-radius: 100px; }
  .o-avatar__badge {
    left: -7.5px;
    padding: 10px;
    border-radius: 50%;
    position: absolute;
    top: -7.5px;
    border: 2px solid #fff;
    background: url(../images/smartsteuer-logo-symbol.png) no-repeat center/100% #fff;
    display: block;
    width: 15px;
    height: 15px; }
  .o-avatar--small {
    height: 30px;
    width: 30px; }

.o-bubble-conversation {
  display: flex;
  flex-direction: column; }

.o-bubble {
  align-self: flex-start;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  max-width: 65%;
  margin-top: 40px;
  position: relative;
  border-radius: 3px;
  background: #f5f4f5;
  line-height: 1.3;
  text-align: left; }
  .o-bubble:first-child {
    margin-top: 0; }
  .o-bubble__inner {
    padding: 12px 15px;
    position: relative; }
    .o-bubble__inner:before {
      content: '';
      display: inline-block;
      position: absolute;
      right: -5px;
      top: 0;
      border-bottom: 10px solid transparent;
      border-left: 10px solid #f5f4f5; }
  .o-bubble__footer {
    display: flex;
    justify-content: center;
    width: 100%;
    border-top: 1px solid #ccc; }
    .o-bubble__footer button {
      padding: 12px;
      width: 50%;
      border-left: 1px solid #ccc;
      font-size: 16px;
      font-size: 1.6rem; }
      .o-bubble__footer button:first-child {
        border-left: none; }
  .o-bubble img:first-child {
    border-radius: 3px 3px 0 0; }
  .o-bubble .o-avatar {
    bottom: 0;
    left: 9px;
    position: absolute;
    border: 3px solid white; }
    .o-bubble .o-avatar--small {
      bottom: -10px;
      left: -10px; }
    .o-bubble .o-avatar__badge {
      display: none; }
  .o-bubble[data-type="answer"] {
    margin-top: 20px;
    align-self: flex-end;
    background-color: #047fcd;
    text-align: right; }
    .o-bubble[data-type="answer"] .o-bubble__inner {
      color: #fff; }
      .o-bubble[data-type="answer"] .o-bubble__inner:before {
        left: -5px;
        right: auto;
        border-left: none;
        border-right: 10px solid #047fcd; }
    .o-bubble[data-type="answer"] .o-avatar {
      left: auto;
      right: -10px; }
  .o-bubble[data-type="answer"] + .o-bubble[data-type="answer"] {
    margin-top: 10px; }
    .o-bubble[data-type="answer"] + .o-bubble[data-type="answer"] .o-bubble__inner:before {
      display: none; }
  .o-bubble[data-collection="true"] .o-avatar {
    display: none; }

